.navBtn::before{
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    width: 0;
    height: 3px;
    background-color:#000;
    transition: all .8s linear;
            
}

.navBtn::after{
    content: '';
    position: absolute;
    top: 100%; 
    right: 0;
    width: 0;
    height: 3px;
    background-color:#000;
    transition: all 0.8s linear;
}

